export default {
  name: 'SysConfigTabs',
  props: {
    activeIndex: {
      type: String,
      default: 'status'
    },
    isCollapse: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      menuItems: [{
        index: 'status',
        icon: 'chart-bar',
        title: ''
      }, {
        index: 'upload',
        icon: 'cloud-upload',
        title: ''
      }, {
        index: 'security',
        icon: 'shield',
        title: ''
      }, {
        index: 'page',
        icon: 'pager',
        title: ''
      }, {
        index: 'others',
        icon: 'cog',
        title: ''
      }]
    };
  },
  methods: {
    toggleCollapse() {
      this.$emit('update:isCollapse', !this.isCollapse);
    },
    checkMobile() {
      const isMobile = window.innerWidth <= 768;
      this.$emit('update:isCollapse', isMobile);
    },
    handleSelect(index) {
      this.$emit('update:activeIndex', index);
    }
  },
  mounted() {
    this.checkMobile();
    window.addEventListener('resize', this.checkMobile);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkMobile);
  }
};