import fetchWithAuth from '@/utils/fetchWithAuth';
import CustomSelect from '@/components/CustomSelect.vue';
export default {
  components: {
    CustomSelect
  },
  data() {
    return {
      // 
      channelFilter: '',
      // 
      channels: [{
        value: 'telegram',
        label: 'Telegram'
      }, {
        value: 'cfr2',
        label: 'CloudFlare R2'
      }, {
        value: 's3',
        label: 'S3'
      }, {
        value: 'discord',
        label: 'Discord'
      }, {
        value: 'huggingface',
        label: 'HuggingFace'
      }],
      //  cfr2 R2  S3 
      addableChannels: [{
        value: 'telegram',
        label: 'Telegram'
      }, {
        value: 's3',
        label: 'S3'
      }, {
        value: 'discord',
        label: 'Discord'
      }, {
        value: 'huggingface',
        label: 'HuggingFace'
      }],
      // 
      telegramSettings: {
        loadBalance: {
          enabled: false
        },
        channels: []
      },
      cfr2Settings: {
        channels: []
      },
      s3Settings: {
        loadBalance: {
          enabled: false
        },
        channels: []
      },
      discordSettings: {
        loadBalance: {
          enabled: false
        },
        channels: []
      },
      huggingfaceSettings: {
        loadBalance: {
          enabled: false
        },
        channels: []
      },
      // 
      showAddDialog: false,
      showDetailDialog: false,
      showEditDialog: false,
      // 
      currentChannelType: '',
      currentChannelIndex: -1,
      currentChannel: null,
      editChannel: {},
      // 
      newChannel: {
        type: '',
        name: '',
        enabled: true,
        // Telegram
        botToken: '',
        chatId: '',
        proxyUrl: '',
        // S3
        endpoint: '',
        bucketName: '',
        region: 'auto',
        accessKeyId: '',
        secretAccessKey: '',
        pathStyle: false,
        quota: {
          enabled: false,
          limitGB: 10,
          threshold: 95
        },
        // Discord
        channelId: '',
        isNitro: false,
        // HuggingFace
        repo: '',
        token: '',
        isPrivate: false
      },
      // 
      addRules: {
        type: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          pattern: /^[\u4e00-\u9fa5a-zA-Z0-9_-]+$/,
          message: '',
          trigger: 'blur'
        }],
        botToken: [{
          required: true,
          message: ' Bot Token',
          trigger: 'blur'
        }],
        chatId: [{
          required: true,
          message: ' Chat ID',
          trigger: 'blur'
        }],
        channelId: [{
          required: true,
          message: ' Channel ID',
          trigger: 'blur'
        }],
        endpoint: [{
          required: true,
          message: ' Endpoint',
          trigger: 'blur'
        }],
        bucketName: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        region: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        accessKeyId: [{
          required: true,
          message: ' ID',
          trigger: 'blur'
        }],
        secretAccessKey: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        repo: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        token: [{
          required: true,
          message: ' Access Token',
          trigger: 'blur'
        }]
      },
      // 
      editRules: {
        name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          pattern: /^[\u4e00-\u9fa5a-zA-Z0-9_-]+$/,
          message: '',
          trigger: 'blur'
        }],
        botToken: [{
          required: true,
          message: ' Bot Token',
          trigger: 'blur'
        }],
        chatId: [{
          required: true,
          message: ' Chat ID',
          trigger: 'blur'
        }],
        channelId: [{
          required: true,
          message: ' Channel ID',
          trigger: 'blur'
        }],
        endpoint: [{
          required: true,
          message: ' Endpoint',
          trigger: 'blur'
        }],
        bucketName: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        region: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        accessKeyId: [{
          required: true,
          message: ' ID',
          trigger: 'blur'
        }],
        secretAccessKey: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        repo: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        token: [{
          required: true,
          message: ' Access Token',
          trigger: 'blur'
        }]
      },
      // 
      quotaStats: {},
      quotaLoading: false,
      // 
      loading: false
    };
  },
  computed: {
    // 
    filterOptions() {
      const iconMap = {
        telegram: 'paper-plane',
        cfr2: 'cloud',
        s3: 'database',
        discord: 'comments',
        huggingface: 'robot'
      };
      return [{
        value: '',
        label: ''
      }, ...this.channels.map(ch => ({
        value: ch.value,
        label: ch.label,
        icon: iconMap[ch.value] || 'server'
      }))];
    },
    // 
    filteredChannels() {
      if (!this.channelFilter) {
        return this.channels;
      }
      return this.channels.filter(ch => ch.value === this.channelFilter);
    }
  },
  methods: {
    // 
    handleCardMouseMove(event, channelType, index) {
      const card = event.currentTarget;
      const rect = card.getBoundingClientRect();
      const x = event.clientX - rect.left;
      const y = event.clientY - rect.top;
      const refKey = `glow-${channelType}-${index}`;
      const glowEl = this.$refs[refKey];
      if (glowEl && glowEl[0]) {
        glowEl[0].style.opacity = '1';
        glowEl[0].style.left = `${x}px`;
        glowEl[0].style.top = `${y}px`;
      }
    },
    handleCardMouseLeave(channelType, index) {
      const refKey = `glow-${channelType}-${index}`;
      const glowEl = this.$refs[refKey];
      if (glowEl && glowEl[0]) {
        glowEl[0].style.opacity = '0';
      }
    },
    // 
    getChannelIcon(type) {
      const icons = {
        telegram: 'paper-plane',
        cfr2: 'cloud',
        s3: 'database',
        discord: 'comments',
        huggingface: 'robot'
      };
      return icons[type] || 'server';
    },
    // 
    getChannelTypeLabel(type) {
      const channel = this.channels.find(c => c.value === type);
      return channel ? channel.label : type;
    },
    // 
    getChannelList(type) {
      return this.getSettings(type)?.channels || [];
    },
    // 
    getSettings(type) {
      const map = {
        telegram: this.telegramSettings,
        cfr2: this.cfr2Settings,
        s3: this.s3Settings,
        discord: this.discordSettings,
        huggingface: this.huggingfaceSettings
      };
      return map[type];
    },
    // 
    hasLoadBalance(type) {
      return ['telegram', 's3', 'discord', 'huggingface'].includes(type);
    },
    // 
    maskText(text, showLength = 4) {
      if (!text) return '';
      if (text.length <= showLength * 2) return '****';
      return text.slice(0, showLength) + '****' + text.slice(-showLength);
    },
    // 
    openDetailDialog(type, index) {
      this.currentChannelType = type;
      this.currentChannelIndex = index;
      this.currentChannel = this.getChannelList(type)[index];
      this.showDetailDialog = true;
    },
    // 
    openEditDialog(type, index) {
      this.currentChannelType = type;
      this.currentChannelIndex = index;
      const channel = this.getChannelList(type)[index];
      this.editChannel = JSON.parse(JSON.stringify(channel));
      //  quota 
      if (!this.editChannel.quota) {
        this.editChannel.quota = {
          enabled: false,
          limitGB: 10,
          threshold: 95
        };
      }
      this.showEditDialog = true;
    },
    // 
    openEditFromDetail() {
      this.showDetailDialog = false;
      this.openEditDialog(this.currentChannelType, this.currentChannelIndex);
    },
    // 
    resetAddForm() {
      this.newChannel = {
        type: '',
        name: '',
        enabled: true,
        botToken: '',
        chatId: '',
        proxyUrl: '',
        endpoint: '',
        bucketName: '',
        region: 'auto',
        accessKeyId: '',
        secretAccessKey: '',
        pathStyle: false,
        quota: {
          enabled: false,
          limitGB: 10,
          threshold: 95
        },
        channelId: '',
        isNitro: false,
        repo: '',
        token: '',
        isPrivate: false
      };
    },
    // 
    resetDetailData() {
      this.currentChannel = null;
    },
    // 
    resetEditData() {
      this.editChannel = {};
    },
    // 
    onChannelTypeChange() {
      //  type  name
      const {
        type,
        name
      } = this.newChannel;
      this.newChannel = {
        type,
        name,
        enabled: true,
        botToken: '',
        chatId: '',
        proxyUrl: '',
        endpoint: '',
        bucketName: '',
        region: 'auto',
        accessKeyId: '',
        secretAccessKey: '',
        pathStyle: false,
        quota: {
          enabled: false,
          limitGB: 10,
          threshold: 95
        },
        channelId: '',
        isNitro: false,
        repo: '',
        token: '',
        isPrivate: false
      };
    },
    // 
    confirmAddChannel() {
      this.$refs.addForm.validate(valid => {
        if (!valid) return;
        const {
          type,
          name
        } = this.newChannel;
        const settings = this.getSettings(type);

        // {type}_env
        const reservedNames = ['Telegram_env', 'R2_env', 'S3_env', 'Discord_env', 'HuggingFace_env'];
        if (reservedNames.includes(name)) {
          this.$message.warning('');
          return;
        }

        // 
        const isDuplicate = settings.channels.some(ch => ch.name === name);
        if (isDuplicate) {
          this.$message.warning('');
          return;
        }
        let newChannelData = {
          id: settings.channels.length + 1,
          name: this.newChannel.name,
          type: type,
          savePath: 'database',
          enabled: true,
          fixed: false
        };

        // 
        if (type === 'telegram') {
          Object.assign(newChannelData, {
            botToken: this.newChannel.botToken,
            chatId: this.newChannel.chatId,
            proxyUrl: this.newChannel.proxyUrl
          });
        } else if (type === 's3') {
          Object.assign(newChannelData, {
            endpoint: this.newChannel.endpoint,
            bucketName: this.newChannel.bucketName,
            region: this.newChannel.region,
            accessKeyId: this.newChannel.accessKeyId,
            secretAccessKey: this.newChannel.secretAccessKey,
            pathStyle: this.newChannel.pathStyle,
            quota: {
              ...this.newChannel.quota
            }
          });
        } else if (type === 'discord') {
          Object.assign(newChannelData, {
            botToken: this.newChannel.botToken,
            channelId: this.newChannel.channelId,
            proxyUrl: this.newChannel.proxyUrl,
            isNitro: this.newChannel.isNitro
          });
        } else if (type === 'huggingface') {
          Object.assign(newChannelData, {
            repo: this.newChannel.repo,
            token: this.newChannel.token,
            isPrivate: this.newChannel.isPrivate
          });
        }
        settings.channels.push(newChannelData);
        this.showAddDialog = false;
        // 
        this.saveSettings();
      });
    },
    // 
    confirmEditChannel() {
      this.$refs.editForm.validate(valid => {
        if (!valid) return;
        const settings = this.getSettings(this.currentChannelType);
        const newName = this.editChannel.name;
        const currentIndex = this.currentChannelIndex;
        const isFixedChannel = this.editChannel.fixed;

        // 
        if (!isFixedChannel) {
          // {type}_env
          const reservedNames = ['Telegram_env', 'R2_env', 'S3_env', 'Discord_env', 'HuggingFace_env'];
          if (reservedNames.includes(newName)) {
            this.$message.warning('');
            return;
          }

          // 
          const isDuplicate = settings.channels.some((ch, idx) => idx !== currentIndex && ch.name === newName);
          if (isDuplicate) {
            this.$message.warning('');
            return;
          }
        }
        settings.channels[this.currentChannelIndex] = {
          ...this.editChannel
        };
        this.showEditDialog = false;
        // 
        this.saveSettings();
      });
    },
    // 
    deleteChannel(type, index) {
      const channel = this.getChannelList(type)[index];
      if (channel.fixed) {
        this.$message.warning('');
        return;
      }
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        const settings = this.getSettings(type);
        settings.channels.splice(index, 1);
        //  id
        settings.channels.forEach((item, i) => {
          item.id = i + 1;
        });
        // 
        this.saveSettings();
      }).catch(() => {});
    },
    saveSettings() {
      // 
      const settings = {
        telegram: this.telegramSettings,
        cfr2: this.cfr2Settings,
        s3: this.s3Settings,
        discord: this.discordSettings,
        huggingface: this.huggingfaceSettings
      };
      fetchWithAuth('/api/manage/sysConfig/upload', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(settings)
      }).then(() => {
        this.$message.success('');
      });
    },
    // 
    async refreshQuota() {
      this.quotaLoading = true;
      try {
        //  POST 
        const response = await fetchWithAuth('/api/manage/quota', {
          method: 'POST'
        });
        const data = await response.json();
        if (data.success) {
          this.quotaStats = data.channelStats || {};
        } else {
          // 
          const getResponse = await fetchWithAuth('/api/manage/quota');
          const getData = await getResponse.json();
          if (getData.success) {
            this.quotaStats = getData.quotaStats || {};
          }
        }
      } catch (error) {
        console.error('Failed to refresh quota stats:', error);
      } finally {
        this.quotaLoading = false;
      }
    },
    // 
    async loadQuotaStats() {
      try {
        const response = await fetchWithAuth('/api/manage/quota');
        const data = await response.json();
        if (data.success) {
          this.quotaStats = data.quotaStats || {};
        }
      } catch (error) {
        console.error('Failed to load quota stats:', error);
      }
    },
    //  (GB)
    getChannelUsedGB(channel) {
      const stats = this.quotaStats[channel.name];
      if (!stats) return 0;
      return (stats.usedMB || 0) / 1024;
    },
    // 
    getQuotaPercentage(channel) {
      const usedGB = this.getChannelUsedGB(channel);
      const limitGB = channel.quota?.limitGB || 10;
      const percentage = usedGB / limitGB * 100;
      return Math.min(100, Math.round(percentage * 10) / 10);
    },
    // 
    getQuotaStatus(channel) {
      const percentage = this.getQuotaPercentage(channel);
      const threshold = channel.quota?.threshold || 95;
      if (percentage >= threshold) return 'exception';
      if (percentage >= 80) return 'warning';
      return 'success';
    },
    // 
    getQuotaText(channel) {
      const usedGB = this.getChannelUsedGB(channel);
      const limitGB = channel.quota?.limitGB || 10;
      return `${usedGB.toFixed(2)} / ${limitGB} GB`;
    },
    // 
    isQuotaExceeded(channel) {
      const percentage = this.getQuotaPercentage(channel);
      const threshold = channel.quota?.threshold || 95;
      return percentage >= threshold;
    },
    // 
    getQuotaStatusText(channel) {
      const percentage = this.getQuotaPercentage(channel);
      const threshold = channel.quota?.threshold || 95;
      if (percentage >= threshold) {
        return `  (${threshold}%)`;
      }
      if (percentage >= 80) {
        return ` `;
      }
      return ` `;
    },
    // 
    async onQuotaEnabledChange(enabled, channel) {
      if (enabled && channel.name) {
        // 
        const stats = this.quotaStats[channel.name];
        if (!stats) {
          // 
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'info'
          }).then(async () => {
            await this.recalculateQuota();
          }).catch(() => {
            this.$message.info('');
          });
        }
      }
    },
    // 
    async recalculateQuota() {
      this.quotaLoading = true;
      try {
        this.$message.info('...');
        const response = await fetchWithAuth('/api/manage/quota', {
          method: 'POST'
        });
        const data = await response.json();
        if (data.success) {
          this.quotaStats = data.channelStats || {};
          this.$message.success('');
        } else {
          this.$message.error(': ' + (data.error || ''));
        }
      } catch (error) {
        console.error('Failed to recalculate quota:', error);
        this.$message.error('');
      } finally {
        this.quotaLoading = false;
      }
    }
  },
  mounted() {
    this.loading = true;
    // 
    fetchWithAuth('/api/manage/sysConfig/upload').then(response => response.json()).then(data => {
      this.telegramSettings = data.telegram;
      //  R2  quota 
      if (data.cfr2 && data.cfr2.channels) {
        data.cfr2.channels = data.cfr2.channels.map(channel => ({
          ...channel,
          quota: channel.quota || {
            enabled: false,
            limitGB: 10,
            threshold: 95
          }
        }));
      }
      this.cfr2Settings = data.cfr2;
      //  S3  quota 
      if (data.s3 && data.s3.channels) {
        data.s3.channels = data.s3.channels.map(channel => ({
          ...channel,
          quota: channel.quota || {
            enabled: false,
            limitGB: 10,
            threshold: 95
          }
        }));
      }
      this.s3Settings = data.s3;
      //  Discord 
      if (data.discord && data.discord.channels) {
        data.discord.channels = data.discord.channels.map(channel => ({
          ...channel,
          proxyUrl: channel.proxyUrl || ''
        }));
      }
      this.discordSettings = data.discord || {
        loadBalance: {},
        channels: []
      };
      //  HuggingFace 
      if (data.huggingface && data.huggingface.channels) {
        data.huggingface.channels = data.huggingface.channels.map(channel => ({
          ...channel,
          isPrivate: channel.isPrivate || false
        }));
      }
      this.huggingfaceSettings = data.huggingface || {
        loadBalance: {},
        channels: []
      };
      // 
      this.loadQuotaStats();
    }).finally(() => {
      this.loading = false;
    });
  }
};