import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.reduce.js";
import "core-js/modules/web.url-search-params.delete.js";
import "core-js/modules/web.url-search-params.has.js";
import "core-js/modules/web.url-search-params.size.js";
import fetchWithAuth from '@/utils/fetchWithAuth';
import packageInfo from '../../package.json';
import { Doughnut } from 'vue-chartjs';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
ChartJS.register(ArcElement, Tooltip, Legend);
export default {
  name: 'SysCogStatus',
  components: {
    Doughnut
  },
  data() {
    return {
      loading: false,
      rebuilding: false,
      backing: false,
      restoring: false,
      indexInfo: {},
      version: packageInfo.version,
      // package.json
      loadErrors: {
        newest: false,
        oldest: false
      },
      // 
      channelColors: ['#8B5CF6', '#EC4899', '#06B6D4', '#10B981', '#F59E0B', '#EF4444', '#6366F1', '#84CC16'],
      // 
      typeColors: ['#10B981', '#3B82F6', '#F59E0B', '#EF4444', '#8B5CF6', '#EC4899', '#06B6D4', '#84CC16']
    };
  },
  computed: {
    // 
    channelChartData() {
      const stats = this.indexInfo.channelStats || {};
      return {
        labels: Object.keys(stats),
        datasets: [{
          data: Object.values(stats),
          backgroundColor: this.channelColors.slice(0, Object.keys(stats).length),
          borderWidth: 0
        }]
      };
    },
    //  - Block""""
    typeChartData() {
      const aggregatedStats = this.aggregatedTypeStats;
      return {
        labels: Object.keys(aggregatedStats),
        datasets: [{
          data: Object.values(aggregatedStats),
          backgroundColor: this.typeColors.slice(0, Object.keys(aggregatedStats).length),
          borderWidth: 0
        }]
      };
    },
    // Block ->  -> 
    aggregatedTypeStats() {
      const stats = this.indexInfo.typeStats || {};
      const aggregatedStats = {};
      for (const [status, count] of Object.entries(stats)) {
        const mappedStatus = status === 'Block' ? '' : '';
        aggregatedStats[mappedStatus] = (aggregatedStats[mappedStatus] || 0) + count;
      }
      return aggregatedStats;
    },
    // 
    chartOptions() {
      return {
        responsive: true,
        maintainAspectRatio: true,
        cutout: '65%',
        hoverOffset: 8,
        layout: {
          padding: 10
        },
        plugins: {
          legend: {
            display: false
          },
          tooltip: {
            backgroundColor: 'rgba(0, 0, 0, 0.8)',
            titleColor: '#fff',
            bodyColor: '#fff',
            padding: 12,
            cornerRadius: 8,
            displayColors: true,
            z: 100,
            callbacks: {
              label: context => {
                const value = context.raw;
                const total = context.dataset.data.reduce((a, b) => a + b, 0);
                const percentage = (value / total * 100).toFixed(1);
                return ` ${value.toLocaleString()} (${percentage}%)`;
              }
            }
          }
        },
        animation: {
          animateRotate: true,
          animateScale: true
        }
      };
    }
  },
  mounted() {
    this.fetchIndexInfo();
  },
  methods: {
    // 
    getChartColor(index) {
      return this.channelColors[index % this.channelColors.length];
    },
    // 
    getTypeChartColor(index) {
      return this.typeColors[index % this.typeColors.length];
    },
    // 
    async fetchIndexInfo() {
      this.loading = true;
      try {
        const response = await fetchWithAuth('/api/manage/list?action=info', {
          method: 'GET'
        });
        if (response.ok) {
          const data = await response.json();
          this.indexInfo = data;
        } else {
          throw new Error('API');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      } finally {
        this.loading = false;
      }
    },
    // 
    async rebuildIndex() {
      this.rebuilding = true;
      try {
        const response = await fetchWithAuth('/api/manage/list?action=rebuild', {
          method: 'GET'
        });
        if (response.ok) {
          this.$message.success('');
          // 
          setTimeout(() => {
            this.fetchIndexInfo();
          }, 3000);
        } else {
          throw new Error('API');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      } finally {
        this.rebuilding = false;
      }
    },
    // 
    async backupData() {
      this.backing = true;
      try {
        const response = await fetchWithAuth('/api/manage/sysConfig/backup?action=backup', {
          method: 'GET'
        });
        if (response.ok) {
          // 
          const blob = await response.blob();
          const url = window.URL.createObjectURL(blob);
          const a = document.createElement('a');
          a.href = url;
          a.download = `imgbed_backup_${new Date().toISOString().split('T')[0]}.json`;
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
          window.URL.revokeObjectURL(url);
          this.$message.success('');
        } else {
          const errorData = await response.json();
          throw new Error(errorData.error || 'API');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error(': ' + error.message);
      } finally {
        this.backing = false;
      }
    },
    // 
    selectRestoreFile() {
      if (this.restoring) return;
      this.$refs.fileInput.click();
    },
    // 
    async handleFileSelect(event) {
      const file = event.target.files[0];
      if (!file) return;
      if (!file.name.endsWith('.json')) {
        this.$message.error('JSON');
        return;
      }

      // 
      try {
        await this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        });
        await this.restoreData(file);
      } catch (error) {
        if (error !== 'cancel') {
          console.error(':', error);
        }
      }

      // 
      event.target.value = '';
    },
    // 
    async restoreData(file) {
      this.restoring = true;
      try {
        const response = await fetchWithAuth('/api/manage/sysConfig/backup?action=restore', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: await file.text()
        });
        if (response.ok) {
          const result = await response.json();
          this.$message.success(` ${result.stats.restoredFiles}  ${result.stats.restoredSettings} `);
          // 
          setTimeout(() => {
            this.fetchIndexInfo();
          }, 1000);
        } else {
          const errorData = await response.json();
          throw new Error(errorData.error || 'API');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error(': ' + error.message);
      } finally {
        this.restoring = false;
      }
    },
    // 
    formatTime(timestamp) {
      if (!timestamp) return '';
      const date = new Date(timestamp);
      return date.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      });
    },
    // 
    getPercentage(value, total) {
      if (!total || total === 0) return 0;
      return Math.round(value / total * 100);
    },
    // 
    getTimeAgo(timestamp) {
      if (!timestamp) return '';
      const now = Date.now();
      const diff = now - timestamp;
      const minutes = Math.floor(diff / 60000);
      const hours = Math.floor(diff / 3600000);
      const days = Math.floor(diff / 86400000);
      if (days > 0) return `${days}`;
      if (hours > 0) return `${hours}`;
      if (minutes > 0) return `${minutes}`;
      return '';
    },
    // 
    handleImageError(type) {
      this.loadErrors[type] = true;
    },
    // 
    isValidPreview(type, file) {
      if (this.loadErrors[type]) return false;
      if (!file?.metadata?.FileType) return false;
      return file.metadata.FileType.includes('image') || file.metadata.FileType.includes('video');
    },
    // 
    openReleases() {
      window.open('https://github.com/MarSeventh/CloudFlare-ImgBed/releases', '_blank');
    },
    // 
    isImageFile(file) {
      if (!file) return false;
      //  FileType 
      if (file.metadata?.FileType?.includes('image')) return true;
      // 
      const fileName = file.metadata?.FileName || file.id || '';
      const extension = fileName.substring(fileName.lastIndexOf('.') + 1).toLowerCase();
      const imageExtensions = ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp', 'svg', 'ico', 'tiff', 'tif', 'avif', 'heic', 'heif'];
      return imageExtensions.includes(extension);
    },
    // 
    isVideoFile(file) {
      if (!file) return false;
      //  FileType 
      if (file.metadata?.FileType?.includes('video')) return true;
      // 
      const fileName = file.metadata?.FileName || file.id || '';
      const extension = fileName.substring(fileName.lastIndexOf('.') + 1).toLowerCase();
      const videoExtensions = ['mp4', 'webm', 'ogg', 'avi', 'mov', 'flv', 'wmv', 'mkv', 'm4v', '3gp', 'mpeg', 'mpg'];
      return videoExtensions.includes(extension);
    },
    // 
    openFileInNewTab(file) {
      if (!file?.id) return;
      window.open('/file/' + file.id, '_blank');
    }
  }
};